.header {
  &__wrap {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 14px 24px;
  }
  &__logo {
    padding: 0 10px;
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-left: auto;
    font-family: $secondary-font;
    font-size: 22px;
    font-weight: 500;
    color: $txt-color;
  }
  &__contacts-call {
    color: $btn-color;
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 0.7px
  }
  &__contact-phone {
    &:visited {
      color: $btn-color;
    }
  }
  &__btn {
    width: 260px;
    height: 47px;
    margin-left: 30px;
    background-color: $btn-color;
    color: #ffffff;
    border-radius: 50px;
    transition: 0.4s;
    &:hover {
      background: darken($btn-color, 5%);
    }
  }
}